import { Component, OnInit, Output } from '@angular/core';
//import {AuthenticationService} from '../authentication.service';
import {ActivatedRoute, Router} from '@angular/router';
import { NgForm, FormControl, FormGroupDirective, Validators } from '@angular/forms';
//import { ErrorStateMatcher } from '@angular/material';
import { User } from '../user';
import { UserRegistrationService } from '../services/user-registration.service';
//import {AppServiceService} from '../app-service.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit {
  
  credentials:any;
  ngOnInit(){
  }
  myimage14: string="assets/img/wallpaper11.jpg";
  constructor(private router: Router,private service: UserRegistrationService){
    let response=this.service.userData();
    response.subscribe((data)=>this.credentials=data);
  }
  onAddPost(form:NgForm){
    if(form.invalid){
      console.log("Invalid");
      return;
    }
    const post = {pssword:form.value.password, email_id:form.value.email};
    console.log(post);
  }
  onLogin(form)
  {
    let flag:boolean=true;
    let email:String=form.value.email;
    let password:String=form.value.password;
    // console.log(email);
    // console.log(password);
    console.log(form.valid);
    for(let i in this.credentials)
    {
      // console.log(this.credentials[i].eMail)
      // console.log(this.credentials[i].password)
      
      if(email==this.credentials[i].eMail&&password==this.credentials[i].password)
      {
        flag=false;
        this.router.navigateByUrl('select');
      }
      else{

      }
    }
    if(flag==true)
    {
      alert("Invalid Credentials");
    }
  }
}


