    bus1.coach_type = "ac";
    bus1.fare = 600;
    bus1.location = "Mohali";
    bus1.seat = 36;
    bus1.time = "10:00 PM";

    this.buses.push(bus1 as Bus);

    let bus2 = new Bus();
    bus2.key = "abc2";
    bus2.name = "C.T.U."
    bus2.coach_type = "non ac";
    bus2.fare = 350;
    bus2.location = "Punjab";
    bus2.seat = 25;
    bus2.time = "9:00 PM";

    this.buses.push(bus2 as Bus);
    
    let bus3 = new Bus();
    bus3.key = "abc3";
    bus3.name = "RAJDHANI"
    bus3.coach_type = "ac";
    bus3.fare = 450;
    bus3.location = "Punjab";
    bus3.seat = 25;
    bus3.time = "9:50 AM";

    this.buses.push(bus3 as Bus);
    
    let bus4 = new Bus();
    bus4.key = "abc4";
    bus4.name = "Sheikhpura"
    bus4.coach_type = "non ac";
    bus4.fare = 350;
    bus4.location = "Chandigarh";
    bus4.seat = 40;
